.addTodo_component {
  margin: 18px 0 32px 0;
}
.addTodo_divContainer {
  display: flex;
  justify-content: space-between;
}
.addTodo_input {
  max-width: 476px;
  width: 100%;
  padding: 20px 12px 19px 12px;
  border-radius: 12px;
  border: 1px solid #BDBDBD;
  font-family: 'Montserrat';
  font-weight: 400;
  font-size: 14px;
  color: var(--text-placeholder);
  outline: none;
  transition: all ease 0.2s;
}
.addTodo_input:focus {
  border: 1px solid var(--blue);
}

.addTodo_button {
  padding: 20px 39px 20px 40px;
  background-color: var(--blue);
  border-radius: 12px;
  border: none;
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 14px;
  color: #FFFFFF;
  cursor: pointer;
  transition: all ease 0.3s;
}

.addTodo_button:hover {
  background-color: var(--blue-hover);
}
